import moment from 'moment';
import _ from 'lodash';

export default class CarInfoConfig {
    static carInfoConfig = [
        {
            key: 'name',
            label: '',
            excelKey: 'Model',
        },
        {
            key: 'brand',
            label: '',
            excelKey: '',
        },
        {
            key: 'specification',
            label: '',
            excelKey: '',
        },
        {
            key: 'model',
            label: '',
            excelKey: 'Model',
        },
        {
            key: 'vId',
            label: '',
            excelKey: '',
        },
        {
            key: 'eId',
            label: '',
            excelKey: '',
        },
        {
            key: 'color',
            label: '',
            excelKey: '',
        },
        {
            key: 'weight',
            label: '',
            excelKey: 'Gross Weight (Kgs)',
        },
        {
            key: 'displacement',
            label: '',
            excelKey: '',
        },
        {
            key: 'productionDate',
            label: '',
            excelKey: '',
            resolveInitialValue: iValue => {
                if (iValue) {
                    return moment('1900')
                        .add(iValue, 'days')
                        .format('YYYY-MM-DD');
                }

                return iValue;
            },
        },
        {
            key: 'taxId',
            label: '',
            excelKey: '',
        },
        {
            key: 'customsClearanceAmount',
            label: '',
            excelKey: '',
        },
        {
            key: 'insteadCollectionAndPaymentAmount',
            label: '',
            excelKey: '',
        },
        {
            key: 'parkingLocation',
            label: '',
            excelKey: '',
        },
        {
            key: 'inboundDate',
            label: '',
            excelKey: '',
            resolveInitialValue: iValue => {
                if (iValue) {
                    return moment('1900')
                        .add(iValue, 'days')
                        .format('YYYY-MM-DD');
                }

                return iValue;
            },
        },
        {
            key: 'outboundDate',
            label: '',
            excelKey: '',
            resolveInitialValue: iValue => {
                if (iValue) {
                    return moment('1900')
                        .add(iValue, 'days')
                        .format('YYYY-MM-DD');
                }

                return iValue;
            },
        },
        {
            key: 'carStyle',
            label: '',
            excelKey: '',
        },
        {
            key: 'BL',
            label: 'B/L',
            excelKey: 'B/L',
        },
        {
            key: 'capacity',
            label: 'Capacity (c.c)',
            excelKey: 'Capacity (c.c)',
            resolveInitialValue: iValue => {
                if (iValue) {
                    const value = parseFloat(iValue);
                    return _.isNaN(value) ? '' : value.toFixed(3);
                }

                return iValue;
            },
        },
    ];
}



