<template>
    <navigate-layout>
        <template slot="header">
            <pt-title type="title" size="4">{{ title }}</pt-title>
        </template>
        <template v-if="!batchAction" slot="middle-area">
            <search-input :types="['vId','eId','note']" :expectedTypes="['vId','vIds']"
                @select="onSelect" ref="searchInput" class="search-input">
            </search-input>
            <el-button type="primary" @click="reset"></el-button>
        </template>
        <template slot="right-area">
            <template v-if="!batchAction && !isNewDetailCarLine">
                <car-excel-importer :business="carsInfoListManager.listBusiness"
                    class="upload-car-info" @importCompleted="onCarImported"></car-excel-importer>
                <car-list-dialog :manager="carsInfoListManager" :visible.sync="insertDialog.show"
                    :business="carsInfoListManager.listBusiness" :fields="carsInfoListManager.listFields"
                    :lines="insertDialog.lines" :lineStatusFunction="carsInfoListManager.compareCarsListForExcelAndResorce"
                    @dialogOk="confirmDialog" :showStatus="true" mode="carsExcel">
                </car-list-dialog>
                <el-button type="primary" @click="onCreateCarInfo"></el-button>
            </template>
        </template>
        <pt-layout slot="content" :fixedBlockVisible="isShowDetail" fixed-block="second"
            :fixed-block-basis="isNewDetailCarLine ? '100%' : '33%'">
            <pt-layout v-show="!(isShowDetail && isNewDetailCarLine)" slot="first"
                verticalMode fixedBlockBasis="50px">
                <div class="flex-row" slot="first">
                    <div class="col grow">
                        <template v-if="batchAction">
                            <el-dropdown @command="onGenerateReceipt">
                                <el-button type="primary" size="small">
                                    
                                    <i class="el-icon-arrow-down el-icon--right"></i>
                                </el-button>
                                <el-dropdown-menu slot="dropdown">
                                    <el-dropdown-item command="inbound"></el-dropdown-item>
                                    <el-dropdown-item command="outbound"></el-dropdown-item>
                                    <el-dropdown-item command="import_declaration"></el-dropdown-item>
                                    <el-dropdown-item command="import_certificate"></el-dropdown-item>
                                </el-dropdown-menu>
                            </el-dropdown>
                            <el-button type="danger" size="small" @click="onBatchDelete"></el-button>
                            <el-button type="default" size="small" @click="onCancelBatchAction"></el-button>
                        </template>
                        <template v-if="!batchAction && carsInfoList.length">
                            <el-button type="primary" size="small" @click="onBatchAction"></el-button>
                        </template>
                    </div>
                </div>
                <cars-info-list slot="second" :manager="carsInfoListManager"
                    @carInfoSelect="onCarInfoSelect" />
            </pt-layout>
            <car-info-detail slot="second" :car-info="detailCarLine" :editable="true"
                :deletable="detailCarLineOriginalStatus" :is-edit="isDetailEdit"
                :business="carsInfoListManager.detailBusiness" :validator="carsInfoListManager.detailValidator"
                @onDetailEdit="onDetailEdit" @onDetailCancel="onDetailCancel"
                @onDetailSave="onDetailSave" @onShowDetail="onShowDetail"
                @deleteCarInfo="deleteCarInfo" />
        </pt-layout>
    </navigate-layout>
</template>

<script>
import CarsInfoListManager from '../js/cars-info-list-manager';

export default {
    name: 'cars-info-list-view',
    data() {
        return {
            carsInfoListManager: new CarsInfoListManager(),
            insertDialog: {
                show: false,
                lines: [],
            },
        };
    },
    created() {
        this.loadCarsInfoList();
    },
    computed: {
        carsInfoList: {
            get() {
                return this.carsInfoListManager.carsInfoList;
            },
        },
        detailCarLine: {
            get() {
                return this.carsInfoListManager.detailCarLine;
            },
        },
        isNewDetailCarLine: {
            get() {
                return this.carsInfoListManager.isNewDetailCarLine;
            },
        },
        isShowDetail: {
            get() {
                return this.carsInfoListManager.isShowDetail;
            },
        },
        isDetailEdit: {
            get() {
                return this.carsInfoListManager.isDetailEdit;
            },
        },
        batchAction: {
            get() {
                return this.carsInfoListManager.batchAction;
            },
        },
        title() {
            // if (this.isCarInfoList) {
            return '';
            // }

            // return '';
        },
        detailCarLineOriginalStatus: {
            get() {
                return `${this.detailCarLine.car.status}` === '0';
            },
        },
    },
    methods: {
        onCarImported(cars) {
            this.carsInfoListManager.getCarsFromExcelAndCompare(cars);
            this.insertDialog.lines = cars;

            this.insertDialog.show = true;
        },
        onShowInsertDialog() {
            this.insertDialog.show = true;
        },
        confirmDialog() {
            this.$prompt('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                inputPattern: /\S+/,
                inputErrorMessage: '',
            }).then(({ value }) => {
                this.carsInfoListManager
                    .saveCarsForExcel(this.insertDialog.lines, value)
                    .then(() => {
                        this.carsInfoListManager.loadCarsInfoList();
                        this.insertDialog.show = false;
                        this.$message({
                            type: 'success',
                            message: '',
                        });
                    })
                    .catch(() => {
                        this.$message({
                            type: 'error',
                            message: '',
                        });
                    });
            });
        },
        loadCarsInfoList() {
            this.carsInfoListManager.loadCarsInfoList();
        },
        reset() {
            this.carsInfoListManager.loadCarsInfoList();
            this.$refs.searchInput.text = '';
        },
        onSelect(item) {
            this.carsInfoListManager.selectCarSInfoList(item);
        },
        onShowDetail(visible) {
            this.carsInfoListManager.onShowDetail(visible);
        },
        onCreateCarInfo() {
            this.carsInfoListManager.goDetail();
            this.onShowDetail(true);
            this.carsInfoListManager.onDetailEdit();
        },
        onCarInfoSelect(item) {
            this.carsInfoListManager.onCarInfoSelect(item);
            this.goCarInfoDetail(item);
        },
        onExcelCarInfoSelect(item) {
            this.carsInfoListManager.onCarInfoSelect(item, 'excel');
        },
        goCarInfoDetail(item) {
            this.carsInfoListManager.goDetail(item);
            this.onShowDetail(true);
        },
        onDetailEdit() {
            this.carsInfoListManager.onDetailEdit();
        },
        onDetailCancel() {
            if (!this.detailCarLine.car.vId) {
                this.onShowDetail(false);
            }

            this.carsInfoListManager.onDetailCancel();
        },
        onDetailSave() {
            if (this.carsInfoListManager.detailValidator.validate()) {
                this.carsInfoListManager
                    .onDetailSave()
                    .then(() => {
                        this.$message({
                            type: 'success',
                            message: '',
                        });
                    })
                    .catch(() => {
                        this.$message({
                            type: 'error',
                            message: '',
                        });
                    });
            }
        },
        deleteCarInfo(item) {
            this.$confirm(', ', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning',
            })
                .then(() => {
                    this.carsInfoListManager
                        .deleteCarInfo([item])
                        .then(() => {
                            this.$message({
                                type: 'success',
                                message: '',
                            });

                            this.emptyListCancelBatchAction();
                        })
                        .catch(() => {
                            this.$message({
                                type: 'error',
                                message: '',
                            });
                        });
                })
                .catch(() => {
                    this.$message({
                        type: 'info',
                        message: '',
                    });
                });
        },
        deleteExcelCarInfo(item) {
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning',
            })
                .then(() => {
                    this.carsInfoListManager
                        .deleteCarInfo([item], 'excel')
                        .then(() => {
                            this.$message({
                                type: 'success',
                                message: '',
                            });

                            this.emptyListCancelBatchAction('excel');
                        })
                        .catch(() => {
                            this.$message({
                                type: 'error',
                                message: '',
                            });
                        });
                })
                .catch(() => {
                    this.$message({
                        type: 'info',
                        message: '',
                    });
                });
        },
        onBatchAction() {
            this.carsInfoListManager.onBatchAction();
        },
        onCancelBatchAction() {
            this.carsInfoListManager.onCancelBatchAction();
        },
        onBatchDelete() {
            if (!this.carsInfoListManager.isHasChecked || !this.carsInfoListManager.isCheckedItemHasOrginalStatus()) {
                this.$message({
                    type: 'info',
                    message: '',
                });
            } else {
                const list = this.carsInfoListManager.fetchCheckedNotOriginalStatusCarInfo();

                this.$confirm(`${list.length}, `, '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning',
                })
                    .then(() => {
                        this.carsInfoListManager
                            .deleteCarInfo(list)
                            .then(() => {
                                this.$message({
                                    type: 'success',
                                    message: '',
                                });

                                this.emptyListCancelBatchAction();
                            })
                            .catch(() => {
                                this.$message({
                                    type: 'error',
                                    message: '',
                                });
                            });
                    })
                    .catch(() => {
                        this.$message({
                            type: 'info',
                            message: '',
                        });
                    });
            }
        },
        onDialogBatchAction() {
            this.carsInfoListManager.onDialogBatchAction();
        },
        onCancelDialogBatchAction() {
            this.carsInfoListManager.onCancelDialogBatchAction();
        },
        onDialogBatchDelete() {
            if (!this.carsInfoListManager.isDialogHasChecked) {
                this.$message({
                    type: 'info',
                    message: '',
                });
            } else {
                this.$confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning',
                })
                    .then(() => {
                        this.carsInfoListManager
                            .deleteCarInfo(this.carsInfoListManager.fetchCheckedCarInfo('excel'), 'excel')
                            .then(() => {
                                this.$message({
                                    type: 'success',
                                    message: '',
                                });

                                this.emptyListCancelBatchAction('excel');
                            })
                            .catch(() => {
                                this.$message({
                                    type: 'error',
                                    message: '',
                                });
                            });
                    })
                    .catch(() => {
                        this.$message({
                            type: 'info',
                            message: '',
                        });
                    });
            }
        },
        emptyListCancelBatchAction(type) {
            const list = this.carsInfoListManager.fetchListByType(type);

            if (!list.length) {
                if (type === 'excel') {
                    this.carsInfoListManager.onCancelDialogBatchAction();
                } else {
                    this.carsInfoListManager.onCancelBatchAction();
                }
            }
        },
        onGenerateReceipt(type) {
            if (!this.carsInfoListManager.checkedItemCanShouldGenerateReceipt(type)) {
                this.$message({
                    type: 'info',
                    message: '',
                });
            } else {
                this.$confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'info',
                })
                    .then(() => {
                        this.carsInfoListManager
                            .onGenerateReceipt(this.carsInfoListManager.fetchCheckedCarInfo(), type)
                            .then(uid => {
                                this.$message({
                                    type: 'success',
                                    message: '',
                                });

                                this.$router.push(`/receipt-list/${uid}`);
                            })
                            .catch(() => {
                                this.$message({
                                    type: 'error',
                                    message: '',
                                });
                            });
                    })
                    .catch(() => {
                        this.$message({
                            type: 'info',
                            message: '',
                        });
                    });
            }
        },
    },
};
</script>

<style lang="scss" scoped>
.search-input {
    width: 360px;
}
</style>

<style>
.upload-car-info .el-button {
    height: 42px;
}
</style>



